#schedule-container {
    border: 1px solid;
    padding: 1px;
}

.pom {
    display: inline-block;
    border: 1px solid;
    margin: 0 2px;
    width: 40px;
    text-align: center;
    border-radius: 3px;
}

.new {
    background-color: rgb(204, 204, 204);
    color: grey;
}

.done {
    background-color: rgb(0, 119, 128);
}

.current {
    background-color: rgb(2, 170, 2);
    color: black;
}

input[type="text"] {
    margin-right: 5px;
    margin-left: 5px;
}

button {
    margin-right: 5px;
}

.label {
    display: inline-block;
    width: 40px;
}

.input {
    float: left;
    width: 150px;
}

.display {
    float: left;
}

#schedule-container {
    height: 50px;
}