/* Sets the containers height and width */
.base-timer {
  margin: 0 auto;
  position: relative;
  text-align: center;
  padding-top: 20px;
}

/* Removes SVG styling that would hide the time label */
.base-timer__circle {
  fill: none;
  stroke: none;
}

/* The SVG path that displays the timer's progress */
.base-timer__path-elapsed {
  stroke-width: 7px;
  stroke: grey;
}

.base-timer__label {
  font-size: 48px;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%,-50%);
}

.base-timer__path-remaining {
  /* Just as thick as the original ring */
  stroke-width: 7px;

  /* Rounds the line endings to create a seamless circle */
  /* stroke-linecap: round; */

  /* Makes sure the animation starts at the top of the circle */
  transform: rotate(90deg);
  transform-origin: center;

  /* Allows the ring to change color when the color value updates */
  stroke: currentColor;
}

.base-timer__path-remaining {
  color: rgb(102, 170, 62);
}

.base-timer__path-remaining.grey {
  color: grey;
}

.paused {
  color: grey;
}

.paused-background {
  stroke: lightgrey;
}

.base-timer__svg {
  /* Flips the svg and makes the animation to move left-to-right */
  transform: scaleX(-1);

  height: 85vh;
}

.transition {
  /* One second aligns with the speed of the countdown timer */
  transition: 1s linear all;
}

.notransition {
  -webkit-transition: none !important;
  -moz-transition: none !important;
  -o-transition: none !important;
  transition: none !important;
}

#timer-controls {
 margin-top: 5%;
}

.completed-blink {
  -webkit-animation: dial-blink 1s infinite;  /* Safari 4+ */
  -moz-animation: dial-blink 1s infinite;  /* Fx 5+ */
  -o-animation: dial-blink 1s infinite;  /* Opera 12+ */
  animation: dial-blink 1s infinite;  /* IE 10+, Fx 29+ */
}

@keyframes dial-blink {
  0%, 49% {
    stroke: rgb(102, 170, 62);
  }
  50%, 100% {
    stroke: grey;
  }
}